<template>
	<el-col :span="24" class="home-header">
		<el-col :span="8" class="home-logo">
			智慧校园
		</el-col>
		<el-col :span="12" class="home-menu">
			<el-menu router :default-active="activeIndex" class="menu-wrap" mode="horizontal">
				<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden&&item.level==1">
					<el-menu-item :index="item.path">{{item.name}}</el-menu-item>
				</template>
			</el-menu>
		</el-col>
		<el-col :span="4" class="home-userinfo">
			<el-dropdown>
			  <span class="el-dropdown-link home-userinfo-inner">
			  	<img src="../images/touxiang.jpg">
			    tresor
			  </span>
			  <el-dropdown-menu slot="dropdown">
			    <el-dropdown-item>我的消息</el-dropdown-item>
			    <el-dropdown-item>设置</el-dropdown-item>
			    <el-dropdown-item divided @click.native="loginout">退出登录</el-dropdown-item>
			  </el-dropdown-menu>
			</el-dropdown>
		</el-col>
	</el-col>
</template>
<script>
	export default {
		methods: {
			loginout: function(){
				this.$store.commit('mutation_bigLevePath', '');
				window.sessionStorage.removeItem('user');
				this.$router.replace({path: '/login'});
			}
		},
		computed: {
			activeIndex: function(){
				let path = this.$route.path;
				this.$store.commit('mutation_bigLevePath', '/' + (path.split('/'))[1]);
				return '/' + (path.split('/'))[1];
			}
		}
	}
</script>


