<template>
	<div>
		<el-menu :default-active="activeIndex" class="el-menu-vertical-demo menu-ul" router unique-opened>
			<template v-for="(item, index) in sidebarMenu" v-if="!item.hidden">
				<el-submenu :index="index+''" v-if="!item.leaf">
			        <template slot="title">{{item.name}}</template>
			        <el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!item.hidden">{{child.name}}</el-menu-item>
			    </el-submenu>
			    <el-menu-item :index="item.path" v-if="item.leaf">{{item.name}}</el-menu-item>
			</template>
	    </el-menu>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				path: '',
				activeIndex: ''
			}
		},
		computed: {
			sidebarMenu: function(){
				let bigLevePath = this.$store.state.bigLevePath,
					routes = this.$router.options.routes,
					list = [];
				routes.forEach(function(item,idx){
					if(item.path == bigLevePath){
						list = item.children;
					}
				});
				
				this.$nextTick(function(){
					this.path = this.$route.path
				})
				return list;
			}
		},
		watch: {
			path: function(val){
				this.activeIndex = val;
			}
		}
	}
</script>


